export default {
  name: "SeckillTabBar",
  props: {
    list: {
      type: Array,
      default: () => [],
    },
    tabId: {
      type: Number,
      default: 0,
    },
  },
  methods: {
    onTabClick(id) {
      // 点击同一个tab
      if (id === this.tabId) {
        return;
      }
      this.$emit("tab-change", id);
    },
  },
};
