import UiButton from "@/components/UiButton";
export default {
  name: "SeckillDialogEnd",
  components: { UiButton },
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    dialogVisible: {
      get() {
        return this.visible;
      },
      set(val) {
        this.$emit("update:visible", val);
      },
    },
  },
  methods: {
    onClose() {
      this.$emit("close");
      this.dialogVisible = false;
    },
  },
};
